import './App.css';
import Link from './Link';
import allLinks from './allLinks';

const headerCaption = "Alo!"
const headerSubCaption = "Menä zvatj Nar..."
const headerDescription = "Ne stejsnäjsä sväzatjsä so mnoj na lüboj iz socialjnyx platform"

function generateLinks(data) {
    return data.map((item, i) => {
        return (
            <div>
                <Link caption={item.caption}
                      description={item.description}
                      link={item.link}
                      />
            </div>

        )
    });
}

function App() {
    return (
        <div className="App">
            <img className="Persona" src="https://static.tumblr.com/om4j1zn/QdIqpswpi/image.png" />
            <div className="Front">
                <div className="Header">
                    <h1 className="Header-Caption">
                        { headerCaption }
                        <br />
                        { headerSubCaption }
                    </h1>
                    <p className="Header-Description">
                        { headerDescription }
                    </p>
                </div>
                <div className="Links">
                    { generateLinks(allLinks) }
                </div>
            </div>
        </div>
    );
}

export default App;
